.home{
    width: 100%;
    color: #002242;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
}
.myname{
    margin-top: -5px;
    margin-bottom: -5px;
    font-size: 50px;
}
.description{
    font-size: 19px;
    width: 500px;
    color: grey;
}
.image{
    height: 500px;
    width: 500px;
}
.home-btn{
    background-color: #FF5F15;
    padding: 10px;
    border: none;
    color: white;
    border-radius: 3px;
    text-decoration: none;
}
@media screen and (max-width: 768px) {
    .home{
        flex-direction: column;
        margin-top: 100px;
        width: 100%;
        height: 120vh;
        padding: 15px;
    }
    .description{
        font-size: 19px;
        width: 400px;
        color: grey;
    }
    .image{
        height: 350px;
        width: 100%;
    }
    
  }
  